"use client";

import { useEffect, useState, useRef } from "react";
import { Chart } from "chart.js/auto";
import type { Chart as ChartType } from "chart.js";
import EnhancedMatrixChart from "../../components/EnhancedMatrixChart";
import LineChart from "../../components/LineChart";
import {
  generateMode4DummyData,
  getMode4DummyErrors,
} from "../../services/mode4DummyService";

export default function Mode4Page() {
  const [businessUnit, setBusinessUnit] = useState("CCS");
  const [useCase, setUseCase] = useState("CC-Di");
  const [errors, setErrors] = useState<any[]>([]);
  const [confusionMatrix, setConfusionMatrix] = useState<any>({});
  const [loading, setLoading] = useState(true);
  const [xaiExplanation, setXaiExplanation] = useState("");
  const [driftHistory, setDriftHistory] = useState<any[]>([]);
  const chartRef = useRef<ChartType | null>(null);

  useEffect(() => {
    const initData = async () => {
      try {
        const mode4Data = generateMode4DummyData();
        console.log("Mode4 dummy data loaded:", mode4Data);
        setConfusionMatrix({
          current: mode4Data,
          reference: mode4Data // Using same data for reference in this example
        });
        setErrors(getMode4DummyErrors(15));
        setXaiExplanation(mode4Data.xaiExplanation);

        // Generate drift history data
        const history = Array.from({ length: 7 }, (_, i) => ({
          date: new Date(Date.now() - (6 - i) * 24 * 60 * 60 * 1000)
            .toISOString()
            .split("T")[0],
          psi: Math.random() * 0.3,
          jsDivergence: Math.random() * 0.25,
          accuracy: 0.85 + Math.random() * 0.1,
        }));
        setDriftHistory(history);
      } catch (error) {
        console.error("Error loading data:", error);
      } finally {
        setLoading(false);
      }
    };
    initData();
  }, []);

  return (
    <div className="bg-gray-900 min-h-screen text-white p-6">
      <div className="max-w-7xl mx-auto">
        {/* Header Section */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-blue-400 mb-2">
            OCTAVE - CLCD
          </h1>
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-4">
            <div className="bg-gray-800 p-4 rounded-lg">
              <h2 className="text-blue-300 mb-1">Business Unit</h2>
              <select
                className="w-full bg-gray-700 text-white p-2 rounded"
                value={businessUnit}
                onChange={(e) => setBusinessUnit(e.target.value)}
              >
                <option value="">Select Business Unit</option>
                <option value="CCS">CCS</option>
                <option value="JMSL">JMSL</option>
              </select>
            </div>
            <div className="bg-gray-800 p-4 rounded-lg">
              <h2 className="text-blue-300 mb-1">Use Case</h2>
              <select
                className="w-full bg-gray-700 text-white p-2 rounded"
                value={useCase}
                onChange={(e) => setUseCase(e.target.value)}
                disabled={!businessUnit}
              >
                <option value="">
                  {businessUnit ? "Select Use Case" : "Select Business Unit first"}
                </option>
                {businessUnit === "CCS" && (
                  <>
                    <option value="CC-Di">CC-Di</option>
                    <option value="MT Promo">MT Promo</option>
                  </>
                )}
                {businessUnit === "JMSL" && (
                  <option value="Customer Churn">Customer Churn</option>
                )}
              </select>
            </div>
            <div className="bg-gray-800 p-4 rounded-lg">
              <h2 className="text-blue-300 mb-1">Short Code</h2>
              <input
                type="text"
                className="w-full bg-gray-700 text-white p-2 rounded"
                value={
                  businessUnit && useCase
                    ? `${businessUnit.substring(0, 2)}-${useCase.substring(0, 2)}`
                    : ""
                }
                readOnly
              />
            </div>
            <div className="bg-gray-800 p-4 rounded-lg">
              <h2 className="text-blue-300 mb-1">Runtime</h2>
              <input
                type="text"
                className="w-full bg-gray-700 text-white p-2 rounded"
                value="2h 45m"
                readOnly
              />
            </div>
          </div>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div className="bg-gray-800 p-4 rounded-lg">
              <h2 className="text-blue-300 mb-1">Current Alert Time</h2>
              <p className="text-xl">2023-03-15T10:45:00Z</p>
            </div>
            <div className="bg-gray-800 p-4 rounded-lg">
              <h2 className="text-blue-300 mb-1">No. of Runtime</h2>
              <p className="text-xl">12</p>
            </div>
            <div className="bg-gray-800 p-4 rounded-lg">
              <h2 className="text-blue-300 mb-1">Alert Keeper</h2>
              <p className="text-xl">John Doe</p>
            </div>
          </div>
        </div>

        {/* Drift-Warning Plot */}
        <div className="bg-gray-800 rounded-xl shadow-md overflow-hidden p-6 mb-6 border border-gray-700">
          <h2 className="text-2xl font-semibold text-blue-300 mb-4">
            Drift-Warning Plot
          </h2>
          <div className="h-64 bg-gray-700 rounded p-4">
            {loading ? (
              <div className="flex items-center justify-center h-full">
                <div className="text-white">Loading plot data...</div>
              </div>
            ) : (
              <div className="relative h-full">
                <canvas
                  id="driftWarningChart"
                  className="w-full h-full"
                  ref={(el) => {
                    if (el && !loading) {
                      const ctx = el.getContext("2d");
                      if (ctx) {
                        if (chartRef.current) {
                          chartRef.current.destroy();
                        }
                        chartRef.current = new Chart(ctx, {
                          type: "line",
                          data: {
                            labels: driftHistory.map((_, i) => `Data Point ${i + 1}`),
                            datasets: [
                              {
                                label: "Drift Status",
                                data: driftHistory.map((item) =>
                                  item.psi >= 0.25
                                    ? 2 // Red
                                    : item.psi >= 0.15
                                    ? 1 // Yellow
                                    : 0 // Green
                                ),
                                borderColor: "rgb(59, 130, 246)",
                                backgroundColor: "rgba(59, 130, 246, 0.1)",
                                tension: 0.1,
                                fill: false,
                                pointBackgroundColor: driftHistory.map((item) =>
                                  item.psi >= 0.25
                                    ? "rgb(239, 68, 68)" // Red
                                    : item.psi >= 0.15
                                    ? "rgb(234, 179, 8)" // Yellow
                                    : "rgb(34, 197, 94)" // Green
                                ),
                              },
                            ],
                          },
                          options: {
                            responsive: true,
                            maintainAspectRatio: false,
                            plugins: {
                              legend: {
                                labels: {
                                  color: "#e5e7eb",
                                },
                              },
                              tooltip: {
                                mode: "index",
                                intersect: false,
                              },
                            },
                            scales: {
                              x: {
                                title: {
                                  display: true,
                                  text: "Data Points",
                                  color: "#93c5fd",
                                  font: {
                                    weight: "bold",
                                  },
                                },
                                grid: {
                                  color: "rgba(255, 255, 255, 0.1)",
                                },
                                ticks: {
                                  color: "#e5e7eb",
                                  font: {
                                    size: 12,
                                  },
                                },
                              },
                              y: {
                                title: {
                                  display: true,
                                  text: "Drift Status",
                                  color: "#93c5fd",
                                  font: {
                                    weight: "bold",
                                  },
                                },
                                beginAtZero: true,
                                grid: {
                                  color: "rgba(255, 255, 255, 0.1)",
                                },
                                ticks: {
                                  color: "#e5e7eb",
                                  font: {
                                    size: 12,
                                  },
                                  callback: (value) => {
                                    if (value === 0) return "Green";
                                    if (value === 1) return "Yellow";
                                    if (value === 2) return "Red";
                                    return value;
                                  },
                                },
                              },
                            },
                          },
                        });
                      }
                    }
                  }}
                />
              </div>
            )}
          </div>
        </div>

        {/* Combined Confusion Matrix and KPIs */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6 mb-8">
          {/* Current Confusion Matrix */}
          <div className="bg-gray-800 p-6 rounded-lg">
            <h2 className="text-2xl font-semibold text-blue-300 mb-4">
              Current Confusion Matrix
            </h2>
            <div className="h-96">
              {loading ? (
                <div className="flex items-center justify-center h-full">
                  <p>Loading...</p>
                </div>
              ) : (
                <EnhancedMatrixChart
                  data={confusionMatrix.current?.matrix || []}
                  labels={["Class A", "Class B"]}
                  title="Current Matrix"
                />
              )}
            </div>
          </div>

          {/* Key Performance Indicators */}
          <div className="bg-gray-800 p-6 rounded-lg">
            <h2 className="text-2xl font-semibold text-blue-300 mb-4">
              Key Performance Indicators
            </h2>
            <div className="grid grid-cols-2 md:grid-cols-3 gap-4 mb-4">
              <div className="bg-blue-900/20 p-4 rounded transition-shadow hover:shadow-lg">
                <p className="text-sm text-blue-200">Precision</p>
                <p className="text-lg font-medium">
                  {confusionMatrix?.current?.precision?.toFixed(2) || "N/A"}
                </p>
              </div>
              <div className="bg-blue-900/20 p-4 rounded transition-shadow hover:shadow-lg">
                <p className="text-sm text-blue-200">Recall</p>
                <p className="text-lg font-medium">
                  {confusionMatrix?.current?.recall?.toFixed(2) || "N/A"}
                </p>
              </div>
              <div className="bg-blue-900/20 p-4 rounded transition-shadow hover:shadow-lg">
                <p className="text-sm text-blue-200">F1 Score</p>
                <p className="text-lg font-medium">
                  {confusionMatrix?.current?.f1?.toFixed(2) || "N/A"}
                </p>
              </div>
              <div className="bg-blue-900/20 p-4 rounded transition-shadow hover:shadow-lg">
                <p className="text-sm text-blue-200">Accuracy (Curr)</p>
                <p className="text-lg font-medium">
                  {confusionMatrix?.current?.accuracy?.toFixed(2) || "N/A"}
                  <span className="text-red-400 text-sm ml-1">(-8.7%)</span>
                </p>
              </div>
            </div>
            <div className="mt-4 bg-yellow-900/20 p-4 rounded">
              <p className="text-sm text-yellow-200">Status</p>
              <p className="text-lg font-medium text-yellow-300">Warning</p>
            </div>
          </div>
        </div>

        {/* XAI Results */}
        <div className="bg-gray-800 p-6 rounded-lg mb-8">
          <h2 className="text-2xl font-semibold text-blue-300 mb-4">
            XAI Result
          </h2>
          <div className="whitespace-pre-line text-gray-300">
            {xaiExplanation || "Loading XAI explanation..."}
          </div>
        </div>

        {/* Error Analysis */}
        <div className="bg-red-900/10 p-6 rounded-lg mb-8">
          <h2 className="text-2xl font-semibold text-red-300 mb-4">
            Error Analysis
          </h2>
          <div className="mb-4">
            <p className="mb-2">
              Overall error rate: <span className="font-bold">16%</span> (was
              8%)
            </p>
            <div className="w-full bg-gray-700 rounded-full h-2.5">
              <div
                className="bg-red-500 h-2.5 rounded-full"
                style={{ width: "16%" }}
              ></div>
            </div>
          </div>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="bg-red-900/20 p-4 rounded">
              <p className="text-sm text-red-200">Class A â†’ B</p>
              <p className="text-lg font-medium">+140%</p>
            </div>
            <div className="bg-red-900/20 p-4 rounded">
              <p className="text-sm text-red-200">Class B â†’ A</p>
              <p className="text-lg font-medium">+166%</p>
            </div>
          </div>
        </div>

        {/* Misclassified Table */}
        <div className="bg-gray-800 p-6 rounded-lg">
          <h2 className="text-2xl font-semibold text-blue-300 mb-4">
            Misclassified Samples
          </h2>
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-700">
              <thead>
                <tr>
                  <th className="px-4 py-3 text-left text-xs font-medium text-blue-200 uppercase tracking-wider">
                    ID
                  </th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-blue-200 uppercase tracking-wider">
                    Predicted
                  </th>
                  <th className="px-4 py-3 text-left text-xs font-medium text-blue-200 uppercase tracking-wider">
                    Actual
                  </th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-700">
                {errors.map((error) => (
                  <tr key={error.id} className="hover:bg-gray-700/50">
                    <td className="px-4 py-3 whitespace-nowrap">
                      {error.id.toString()}
                    </td>
                    <td className="px-4 py-3 whitespace-nowrap">
                      {error.predicted}
                    </td>
                    <td className="px-4 py-3 whitespace-nowrap">
                      {error.actual}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  );
}