'use client'
import React from 'react';
import { Chart } from 'react-chartjs-2';
import { MatrixController, MatrixElement } from 'chartjs-chart-matrix';
import {
  Chart as ChartJS,
  LinearScale,
  CategoryScale,
  Tooltip,
  Legend
} from 'chart.js';

ChartJS.register(
  MatrixController,
  MatrixElement,
  LinearScale,
  CategoryScale,
  Tooltip,
  Legend
);

interface MatrixChartProps {
  data: number[][];
  labels: string[];
  title: string;
  isPercentage?: boolean;
}

export default function EnhancedMatrixChart({
  data,
  labels,
  title,
  isPercentage = false
}: MatrixChartProps) {
  const getColor = (value: number) => {
    if (isPercentage) {
      // Heatmap for percentage changes (red to white to blue)
      const normalized = Math.min(Math.max(value / 100, -1), 1);
      if (normalized < 0) {
        const intensity = Math.abs(normalized);
        return `rgba(220, ${Math.round(100 * (1 - intensity))}, ${Math.round(100 * (1 - intensity))}, 0.8)`;
      } else if (normalized > 0) {
        const intensity = normalized;
        return `rgba(${Math.round(255 * (1 - intensity))}, ${Math.round(255 * (1 - intensity))}, 255, 0.7)`;
      }
      return 'rgba(255, 255, 255, 0.7)'; // White for zero
    }
    // Heatmap for absolute values (light to dark blue)
    const maxVal = Math.max(...data.flat());
    const normalized = maxVal > 0 ? value / maxVal : 0;
    return `rgba(0, 0, ${Math.round(255 * (0.2 + 0.8 * normalized))}, 0.8)`;
  };

  // Generate dynamic labels based on matrix dimensions
  const generateLabels = (matrix: number[][]) => {
    return matrix[0].map((_, i) => `Class ${String.fromCharCode(65 + i)}`);
  };

  const dynamicLabels = generateLabels(data);
  const chartData = {
    datasets: [{
      label: title,
      data: data.map((row, y) => 
        row.map((value, x) => ({
          x: dynamicLabels[x],
          y: dynamicLabels[dynamicLabels.length - 1 - y], // Reverse y-axis
          value
        }))
      ).flat(),
      backgroundColor: (ctx: any) => getColor(ctx.dataset.data[ctx.dataIndex].value),
      borderColor: 'rgba(255, 255, 255, 0.2)',
      borderWidth: 1,
      width: ({chart}: any) => {
        if (!chart.chartArea) return 100;
        const cols = data[0]?.length || 1;
        return Math.min(150, (chart.chartArea.width - 60) / Math.pow(cols, 1.4) * 2.0);
      },
      height: ({chart}: any) => {
        if (!chart.chartArea) return 60;
        return Math.min(100, (chart.chartArea.height - 60) / Math.pow(data.length, 1.3) * 1.8);
      }
    }]
  };

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      legend: { display: false },
      tooltip: {
        callbacks: {
          label: (ctx: any) => {
            const {x, y, value} = ctx.dataset.data[ctx.dataIndex];
            // Note: x and y are swapped in the display
            return `${y} â†’ ${x}: ${value.toFixed(isPercentage ? 1 : 2)}${isPercentage ? '%' : ''}`;
          }
        }
      }
    },
    scales: {
      x: {
        type: 'category',
        labels,
        offset: true,
        grid: { display: false },
        ticks: { 
          color: '#e5e7eb',
          font: { size: 12 }
        }
      },
      y: {
        type: 'category',
        labels,
        offset: true,
        grid: { display: false },
        ticks: { 
          color: '#e5e7eb',
          font: { size: 12 }
        }
      }
    }
  };

  return (
    <div className="relative h-full w-full">
      <h3 className="text-lg font-medium text-blue-200 mb-2">{title}</h3>
      <div 
        className="h-full w-full" 
        style={{ 
          minHeight: `${data.length * 50}px`,
          minWidth: `${data[0]?.length * 50}px` 
        }}
      >
        <Chart 
          type="matrix" 
          data={chartData} 
          options={options}
          plugins={[{
            id: 'matrix-values',
            beforeDraw(chart) {
              const {ctx, data, chartArea} = chart;
              if (!chartArea) return;
              
              ctx.font = 'bold 28px Arial';
              ctx.textAlign = 'center';
              ctx.textBaseline = 'middle';
              
              data.datasets[0].data.forEach((point: any, i: number) => {
                const meta = chart.getDatasetMeta(0);
                const element = meta.data[i] as any;
                
                if (element.hidden) return;
                
                const value = point.value;
                const x = element.x;
                const y = element.y;
                const width = element.width;
                const height = element.height;
                
                const bgColor = getColor(value);
                const rgb = bgColor.match(/\d+/g);
                if (rgb) {
                  const brightness = (parseInt(rgb[0]) * 299 + 
                                    parseInt(rgb[1]) * 587 + 
                                    parseInt(rgb[2]) * 114) / 1000;
                  ctx.fillStyle = brightness > 125 ? '#000' : '#fff';
                  const text = value.toFixed(isPercentage ? 1 : 2) + (isPercentage ? '%' : '');
                  
                  // Draw text on top of colored boxes
                  ctx.save();
                  ctx.globalCompositeOperation = 'source-over';
                  // Add text shadow for better visibility
                  ctx.shadowColor = 'rgba(0, 0, 0, 0.7)';
                  ctx.shadowBlur = 3;
                  ctx.shadowOffsetX = 1;
                  ctx.shadowOffsetY = 1;
                  // Get dimensions from the original matrix data passed to component
                  const matrixData = data.datasets[0].data as Array<{x: string, y: string, value: number}>;
                  const rows = new Set(matrixData.map((item: {x: string, y: string, value: number}) => item.y)).size;
                  const cols = new Set(matrixData.map((item: {x: string, y: string, value: number}) => item.x)).size;
                  const maxDimension = Math.max(rows, cols);
                  const fontSize = Math.max(12, 30 / Math.pow(maxDimension, 0.3));
                  ctx.font = `bold ${fontSize}px Arial`;
                  ctx.fillText(
                    text,
                    x + width/2, // Center horizontally
                    y + height/2 // Center vertically
                  );
                  ctx.restore();
                }
              });
            }
          }]}
        />
      </div>
    </div>
  );
}